@use "base/vars" as v;
@use "base/mixins" as m;

.gallery {
  padding: 5rem 0;
  h3 {
    text-align: center;
  }
}

.gallery-images {
  img {
    cursor: pointer;
  }
  @include m.reset-list;
  @include m.grid(2, 2rem);

  @include m.phone {
    @include m.grid(3, 2rem);
  }
  @include m.tablet {
    @include m.grid(4, 2rem);
  }
  @include m.desktop {
    @include m.grid(6, 2rem);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  width: 100%;
  height: 100%;

  display: grid;
  place-items: center;

  background-color: rgb(0 0 0 / 0.5);

  picture {
    display: block;
    max-width: 90rem;
  }
}

.close-button {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: v.$whiteColor;
  color: v.$blackColor;
  display: grid;
  place-items: center;
  font-weight: 700;
  user-select: none;

  &:hover {
    cursor: pointer;
  }
}
