html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fix-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #4cb8b3;
}
@media (min-width: 768px) {
  .header.fixed {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .content-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .content-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .nav-main {
    display: flex;
    gap: 2rem;
  }
}
.nav-main a {
  color: #ffffff;
  font-size: 2.4rem;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .nav-main a {
    margin: 0;
  }
}
.nav-main a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(47, 0, 71);
  background: linear-gradient(270deg, rgba(47, 0, 71, 0.4009978992) 0%, rgba(255, 0, 181, 0.3981967787) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.content-video {
  z-index: 10;
}
.content-video h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 0;
}
.content-video p {
  background-color: #f53756;
  color: #ffffff;
  display: inline-block;
  padding: 1rem 2rem;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.content-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .content-festival {
    margin-top: 0;
  }
}
.content-festival h2 {
  margin-bottom: 2rem;
}
.content-festival .date {
  color: #f53756;
  font-weight: 700;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .stages-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.stages-container p {
  color: #000000;
}

.stage {
  margin-bottom: 1rem;
  padding: 2rem;
}
.stage p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.rock .stage-name:after {
  background-image: url(../img/rock_icon.png);
}

.edm .stage-name:after {
  background-image: url(../img/edm_icon.png);
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.bg-yellow {
  background-color: #fef46d;
}
.bg-yellow li:nth-child(odd) {
  background-color: #eada02;
}

.bg-green {
  background-color: #4cb8b3;
}
.bg-green li:nth-child(odd) {
  background-color: #3b9691;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.gallery-images img {
  cursor: pointer;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 90rem;
}

.close-button {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
  user-select: none;
}
.close-button:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .container-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass {
  color: #000000;
}
.pass-name {
  text-align: center;
  font-size: 2.8rem;
  color: #ffffff;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.pass-header {
  background-color: #f53756;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass-body {
  background-color: #ffffff;
}
.pass-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass-footer {
  background-color: #752f97;
}
.pass-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}